const pageTitles = {
  home: '',
  about: '',
  logview: '',
  visits: ''
}
const topMenus = [{
  name: 'home',
  text: pageTitles.home,
  icon: 'home'
}, {
  name: 'about',
  text: '',
  icon: 'ios-help'
}]
const leftMenus = {
  home: [{
    text: '',
    children: [{
      name: 'logview',
      text: pageTitles.logview,
      icon: 'ios-paper-outline',
      color: '#0c9'
    }, {
      name: 'visits',
      text: pageTitles.visits,
      icon: 'ios-pulse',
      color: '#39f'
    }]
  }],
  about: [{
    text: '',
    children: [{
      name: 'about',
      text: pageTitles.about,
      icon: 'information-circled'
    }]
  }]
}

export default {
  activeTopMenu: '',
  activeLeftMenu: '',
  topMenus,
  leftMenus,
  pageTitles,
  curPageName: ''
}



